.row {
  margin-left: 20px;
  color: white;
}
.posters {
  display: flex;
  padding: 20px;
  overflow-y: hidden;
  overflow-x: scroll;
}
.poster {
  max-height: 220px;
  margin-right: 10px;
  cursor: pointer;
}
.smallPoster {
  max-height: 150px;
  margin-right: 10px;
  cursor: pointer;
}
.poster:hover {
  transform: scale(1.1);
}
.smallPoster:hover {
  transform: scale(1.1);
}
.closeButton {
  color: white;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 5px;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  cursor: pointer;
  margin-top: 10px;
}
.cButton {
  text-align: center;
}
.closeButton:hover {
  color: black;
  background-color: #e6e6e6;
}
